import * as React from "react";
import { Breadcrumbs, Button, List, ListItem, ListItemSecondaryAction, ListItemText, Typography } from "@material-ui/core";
import Navbar from "shared/components/ui/Navbar";
import { Link } from "gatsby";
import { useFetcher } from "shared/components/ajax";
import AddIcon from '@material-ui/icons/Edit';
import { navigate } from "@reach/router";
import { getSearchParam } from "shared/components/util";
import { keyedCategories } from ".";
import Defer from "shared/components/ui/Defer";

const Page = () => {
    const id = getSearchParam().get('id');
    const item = useFetcher(id && '/desa/ask/' + id);
    return (
        <div>
            <Navbar />
            <List>
                <ListItem>
                    <Breadcrumbs>
                        <Link color="inherit" to="/user/">
                            Layanan Desa
                        </Link>
                        <Link color="inherit" to="/desa/komplain/">
                            Layanan Komplain
                        </Link>
                        <Typography color="textPrimary">{'Detail'}</Typography>
                    </Breadcrumbs>
                    {item && item.data.owned && item.data.status === 'open' &&
                        <ListItemSecondaryAction>
                            <Button startIcon={<AddIcon />} onClick={() => navigate('/desa/komplain/post?id=' + id)} variant="contained" color="primary">
                                Edit
                            </Button>
                        </ListItemSecondaryAction>}
                </ListItem>
                <Defer data={item}>
                    {() => (
                        <>
                            <ListItem divider>
                                {keyedCategories[item.data.category] && React.createElement(keyedCategories[item.data.category].icon, { style: { fontSize: '2em', marginRight: 15 } })}
                                <ListItemText primary={keyedCategories[item.data.category]?.title} secondary={keyedCategories[item.data.category]?.desc} />
                            </ListItem>
                            <ListItem divider>
                                <div>
                                    <Typography variant="caption">
                                        Pesan
                                    </Typography>
                                    <Typography>
                                        {item.data.message}
                                    </Typography>
                                </div>
                            </ListItem>
                            <ListItem divider>
                                <div>
                                    <Typography variant="caption">
                                        Respon
                                    </Typography>
                                    {item.data.response ? <Typography>
                                        {item.data.response}
                                    </Typography> : <Typography style={{ fontStyle: 'italic' }}>
                                        Belum ada respon
                                    </Typography>}
                                </div>
                            </ListItem>
                        </>
                    )}
                </Defer>
            </List>
        </div >
    );
}

export default Page
